html,
body {
	font-family: 'TT Norms Pro', sans-serif;
	font-size: 20px;
	line-height: 1.5;
	font-weight: normal;
	color: $text-color;
	background: #F5F5F5;
	min-width: 360px;
}

p {
	margin: 0 0 20px 0;
}

p:last-child {
	margin: 0;
}

a,
a:focus {
	color: #0094FF;
	text-decoration: underline;
	transition: color .15s ease;
	outline: none !important;
}

a:hover {
	text-decoration: none;
}

.over-wrapper ul {
	list-style: none;
	margin: 0 0 20px 0;
	padding: 0;
}

.over-wrapper ul > li {
	margin: 0 0 20px 0;
	position: relative;
	padding: 0 0 0 30px;
}

.over-wrapper ul > li:before {
	content: "";
	background: url(../images/li-check.svg) 0 0 no-repeat;
	width: 14px;
	height: 10px;
	position: absolute;
	left: 0;
	top: 10px;
}

// Buttons

.btn,
.btn:focus {
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	background-color: transparent;
	text-decoration: none !important;
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
	border-radius: 0;
	vertical-align: top;
	border: none;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	text-align: center;
	transition: background-color .2s ease, color .2s ease, box-shadow .2s ease, border-color .2s ease, box-shadow .2s ease, font-size .2s ease;
	-webkit-appearance: none !important;
}

.btn-login,
.btn-login:focus {
	font-size: 18px;
	line-height: 1.5;
	font-weight: normal;
	color: #39353E;
	border: 1px solid #39353E;
	border-radius: 40px;
	padding: 4px 27px 7px 27px;
}

.btn-login span {
	display: inline-block;
	position: relative;
	padding: 0 0 0 30px;
}

.btn-login span:before {
	content: "";
	background: url(../images/btn-login.svg) 0 0 no-repeat;
	width: 18px;
	height: 21px;
	position: absolute;
	left: 0;
	top: 4px;
}

.btn-1,
.btn-1:focus {
	font-size: 20px;
	line-height: 1.5;
	font-weight: 500;
	color: #39353E;
	background: #fff;
	border: 1px solid #000000;
	border-radius: 30px;
	padding: 12px 29px 16px 29px;
}



.btn-1-small,
.btn-1-small:focus {
	font-size: 16px;
	padding: 10px 44px 9px 44px;
}

.btn-1-medium,
.btn-1-medium:focus {
	font-size: 18px;
	line-height: 30px;
}

.btn-2,
.btn-2:focus {
	font-size: 20px;
	line-height: 1.5;
	font-weight: 500;
	color: #fff !important;
	background: #FF6A52;
	border-radius: 30px;
	padding: 13px 30px 17px 30px;
}

.btn-2-big,
.btn-2-big:focus {
	font-size: 22px;
	line-height: 30px;
}

.btn-3,
.btn-3:focus {
	font-size: 20px;
	line-height: 1.5;
	font-weight: 500;
	color: #fff !important;
	background: #0094FF;
	border-radius: 30px;
	padding: 13px 30px 17px 30px;
}

.btn-3-smallest,
.btn-3-smallest:focus {
	font-size: 16px;
	padding: 8px 25px 8px 25px;
}

// Buttons END

// Sliders

.slick-arrow {
	position: absolute;
	z-index: 10;
	border: none;
	-webkit-appearance: none;
	width: 50px;
	height: 50px;
	background: #0094FF;
	border-radius: 50%;
	transition: all .2s ease;
	font-size: 0;
	line-height: 0;
	color: transparent;
	text-indent: -20000px;
	overflow: hidden;
	top: 50%;
	margin-top: -25px;
	cursor: pointer;
	outline: none !important;
}

.slick-arrow.slick-disabled {
	opacity: .2 !important;
	background: #0094FF !important;
}

.slick-arrow:before {
	content: "";
	width: 14px;
	height: 21px;
	background-repeat: no-repeat;
	background-position: 0 0;
	position: absolute;
	left: 50%;
	top: 50%;
	margin: -11px 0 0 0;
}

.slick-prev:before {
	left: 16px;
	background-image: url(../images/slick-prev.svg);
}

.slick-next:before {
	left: auto;
	right: 16px;
	background-image: url(../images/slick-next.svg);
}

.slick-prev {
	left: -10px;
}

.slick-next {
	right: -10px;
}

.slider-wrapper {
	margin: 0 -15px;
}

.slick-slide {
	margin: 0 15px;
}

