.modal-header {
	display: block;
	border: none;
	padding: 0 30px 0 0;
	margin: 0 0 14px 0;
}

.modal-header h2,
.modal-header .h2 {
	font-size: 40px;
	margin: 0;
}

.modal .close {
	position: absolute;
	right: 40px;
	top: 40px;
	opacity: 1 !important;
	width: 32px;
	height: 32px;
	opacity: 1 !important;
	cursor: pointer;
	z-index: 10;
	outline: none !important;
	transition: all .2s ease;
}

.modal .close:before {
	content: "";
	background: url(../images/close.svg) 0 0 no-repeat;
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	transition: all .2s ease;
}

.modal .close:after {
	content: "";
	background: url(../images/close-hover.svg) 0 0 no-repeat;
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	transition: all .2s ease;
	opacity: 0;
}



