.form-group {
	position: relative;
	margin: 0 0 19px 0;
	padding: 10px 0 0 0;
}

label.placeholder {
	font-family: 'TT Norms Pro';
	font-size: 18px;
	line-height: 1.5;
	font-weight: normal;
	color: #39353E;
	display: block;
	position: absolute;
	top: 10px;
	transition: all .3s ease;
	margin: 0;
	padding: 15px 27px 16px 27px;
	border: 1px solid transparent;
}

label.placeholder.active {
	font-size: 14px;
	line-height: 18px;
	top: 0;
	padding: 0 10px 0 10px;
	margin-left: 17px;
	border-width: 0;
	background: #fff;
}

label.error {
	position: absolute;
	font-size: 14px;
	line-height: 18px;
	color: $err-color;
	margin: 0 0 0 0;
}

input[type=text],
input[type=email],
input[type=password],
textarea {
	border: 1px solid #39353E;
	background: none;
	transition: all .2s ease;
	font-family: 'TT Norms Pro';
	font-size: 18px;
	line-height: 1.5;
	font-weight: bold;
	color: #39353E;
	padding: 15px 27px 16px 27px;
	width: 100%;
	display: block;
	outline: none !important;
}

textarea {
	resize: none;
}

input[type=text].error,
input[type=email].error,
input[type=password].error,
textarea.error {
	border-color: $err-color;
	color: $err-color;
}

.form-footer {
	margin: 19px 0 0 0;
}

.order-form .form-footer .btn {
	width: 100%;
}

.feedback-form .form-footer .btn {
	min-width: 285px;
}

.form-col-name {
	flex: 0 0 58.4%;
	max-width: 58.4%;
}

.form-col-phone {
	flex: 0 0 41.6%;
	max-width: 41.6%;
}

.form-agree {
	font-size: 14px;
	line-height: 18px;
	color: #666666;
	margin: 25px 0 0 0;
}

.order-form .form-agree {
	max-width: 360px;
}

.form-checkbox input[type=checkbox] {
	visibility: hidden;
	position: absolute;
}

.form-checkbox input[type=checkbox] + label {
	font-size: 18px;
	line-height: 1.5;
	font-weight: normal;
	color: #39353E;
	padding: 0 0 0 24px;
	position: relative;
	margin: 0;
	display: block;
	cursor: pointer;
}

.form-checkbox input[type=checkbox] + label:before {
	content: "";
	width: 16px;
	height: 16px;
	border: 1px solid #39353E;
	position: absolute;
	left: 0;
	top: 6px;
	background: url(../images/checkbox.svg) center 2px no-repeat;
	background-size: 0 0;
}

.form-checkbox input[type=checkbox]:checked + label:before {
	background-size: 10px 9px;
}

.forgot-link-wrapper {
	font-size: 18px;
	line-height: 1.5;
}

// Calc checkboxes

.calc-checkboxes {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -7px -10px -7px;
}

.calc-checkbox {
	padding: 0 7px 0 7px;
	margin-bottom: 10px;
}

.calc-checkboxes input[type=checkbox] {
	position: absolute;
	visibility: hidden;
}

.calc-checkboxes input[type=checkbox] + label {
	display: block;
	border: 1px solid #0094FF;
	border-radius: 25px;
	color: #0094FF;
	text-decoration: none;
	transition: all .25s ease;
	padding: 13px 30px 11px 30px;
	font-size: 16px;
	font-weight: 500;
	margin: 0;
	cursor: pointer;
}

.calc-checkboxes input[type=checkbox]:checked + label {
	color: #fff;
	background: #0094FF;
}

// Calc checkboxes END

@media (max-width: 767px) {


	label.placeholder {
		font-size: 16px;
		line-height: 20px;
		padding: 12px 15px;
	}

	label.placeholder.active {
		font-size: 12px;
		line-height: 18px;
	}

	input[type=text],
	input[type=email],
	input[type=password],
	textarea {
		font-size: 15px;
		line-height: 20px;
		padding: 12px 15px;
	}

}