@media (max-width: 767px) {

	.section-top-info,
	.section-profession-header .page-header-pic {
		transform: rotate(-15deg) !important;
		opacity: 1 !important;
	}

}

.section-top-info {
	opacity: 0;
	transform: scale(0);
}

.section-profession-header .page-header-pic {
	opacity: 0;
	transform: scale(0);
}