	}

	.sticky-block {
		top: auto !important;
		width: auto !important;
		margin-top: 0 !important;
	}

	.navbar-wrapper {
		display: none;
		position: absolute;
		border: 1px solid #000000;
		padding: 22px 29px 22px 29px;
		background: #fff;
		width: 250px;
		top: 100%;
		left: -29px;
		margin-top: 10px;
		box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.1);
	}

	ul.navbar-nav {
		display: block;
		margin: 0 0 -15px 0;
		font-size: 18px;
	}

	ul.navbar-nav > li {
		margin: 0 0 15px 0;
		padding: 0;
	}

	.navbar-trigger {
		position: relative;
		width: 20px;
		height: 20px;
		cursor: pointer;
		display: block;
		margin: 0 auto;
	}

	.navbar-trigger span {
		height: 1px;
		background: #39353E;
		position: absolute;
		left: 0;
		right: 0;
		transition: all .3s ease;
	}

	.navbar-trigger:hover span,
	.navbar-trigger.active span {
		background: $blue-color;
	}

	.navbar-trigger span:nth-child(1) {
		top: 2px;
	}

	.navbar-trigger span:nth-child(2) {
		top: 50%;
		background: $blue-color;
	}

	.navbar-trigger span:nth-child(3) {
		bottom: 1px;
	}

	.navbar-trigger.active span:nth-child(1) {
		top: 50%;
		transform: rotate(-45deg);
	}

	.navbar-trigger.active span:nth-child(2) {
		opacity: 0;
	}

	.navbar-trigger.active span:nth-child(3) {
		bottom: 50%;
		margin-bottom: -1px;
		transform: rotate(45deg);
	}

	.article-date {
		margin-top: 20px;
